@import url(https://fonts.googleapis.com/css?family=Martel|Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Anton|Passion+One&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

.details {
  color: #9ab;
  padding: 2rem;
  margin-top: 1rem;
}

h1,
h2 {
  color: rgb(228, 228, 228);
}

.description {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}

.description img {
  border-radius: 4px;
  height: 400px;
  width: auto;
  border: solid rgb(94, 94, 94) 1px;
  margin: 0 auto;
}

.info {
  max-width: 800px;
}

.tag {
  font-size: 1.7rem;
  font-style: italic;
  margin-bottom: 1.5rem;
}

.description h1 {
  margin-top: 1rem;
  font-size: 3.5rem;
  margin-bottom: 0;
}

.plot {
  font-size: 1.5rem;
}

.reviews {
  text-align: center;
  font-size: 1.5rem;
}

.reviews-header {
  margin-bottom: 2rem;
}

.reviews-header h2 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.review {
  color: #9ab;
  margin-bottom: 3rem;
}

.review h3 {
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 0.5rem;
  color: #9ab;
}

.rating {
  display: flex;
  justify-content: center;
  margin-bottom: 0.5rem;
}

.desc {
  font-size: 1.5rem;
}

.result-card {
  width: 210px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  padding-bottom: 1.5rem;
  transition: 0.04s;
  margin-bottom: 3rem;
  margin-left: 1.5rem;
  margin-right: 1.5rem;
}

.release-date {
  color: rgba(255, 255, 255, 0.8);
}

.result-card:hover {
  border: 1px solid rgba(255, 255, 255, 0.7);
  cursor: pointer;
}

.result-card h2 {
  margin-bottom: 0.5rem;
  margin-left: 4px;
  margin-right: 4px;
}

.result-card img {
  width: 100%;
  margin-bottom: 1rem;
}

.search {
  margin-top: 1rem;
  text-align: center;
}

.results {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.navbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0.8rem;
}

.nav-logo {
  font-family: 'Passion One';
  font-size: 3.5rem;
  color: rgba(255, 255, 255, 0.9);
  transition: 0.1s;
}

.nav-logo:hover {
  color: rgba(255, 255, 255, 0.65);
  cursor: pointer;
}

.nav-search {
  width: auto;
  display: flex;
  align-items: center;
}

.home {
  margin-top: 1rem;
  text-align: center;
}

.home-search {
  width: 80%;
  margin: 0 auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

